<template>
    <div class="notice">
        <indexHeader></indexHeader>
        <div class="pdf">
          <!-- <embed src="https://www.castprofit.com/h5/paper.pdf" width="100%" height="800"> -->
		   <iframe :src="pdfurl" frameborder="0" width="100%" height="800"></iframe>
		   <!-- <embed :src="pdfurl" width="100%" height="800"> -->
		   <p class="arrow" style="text-align:center;color:#000;margin: 20px 0 20px 0;">
		  </p>
        </div>
    </div>
</template>
<script>
import indexHeader from "@/view/indexHeader";
export default {
  name: "noticeList",
  components: { indexHeader },
  data() {
    return {
		pdfurl:'',
		id:''
    };
  },
  created: function() {
	this.getPdf();
  },
  methods: {
	getPdf() {
	  this.id = this.$route.query.id;
	  this.$http({
	    url: '/api/' + 'news/detail',
	    method:'post',
	    data:{id:this.id},
	  }).then(res => {
		  console.log(res)
	    if (res.data.type == "ok") {
	      // console.log(222);
		  var aurl = res.data.message.content
		  var reg = /<a[^>]*href[=\"\'\s]+([^\"\']*)[\"\']?[^>]*>/gi;
		 while(reg.exec(aurl));
		 this.pdfurl = RegExp.$1;
	    }
	  });
	},
  }
};
</script>
<style lang="scss" scoped>
.pdf{
  text-align:center;
  width: 100%;
  padding-bottom: 15px;
  background: #51565b !important;
}
</style>



